import * as React from "react";
import { useState, useEffect } from "react";
import { useMotionValue, Reorder, useDragControls, useAnimation } from "framer-motion";
import { useRaisedShadow } from "./use-raised-shadow";
import { motion } from "framer-motion";

interface IProps {
  item: {
    quiz: string;
    id: string;
    type: string;
    score: number;
  };
  index: number;
  color: string;
  dragState: Function;
  isSelected: boolean;
}

export const Item: React.FC<IProps> = ({ item, index, color, dragState, isSelected }) => {
  const y = useMotionValue(0);
  const boxShadow = useRaisedShadow(y);
  const maxScore = 4;
  item.score = maxScore - index;

  const controls = useAnimation();
  const dragControls = useDragControls()
  
  const onDrag = (e:any, y:number) => {
    // console.log('onDrag', e, y);

    dragState(true);
  }

  const onDragEnd = (e:any) => {
    // console.log('onDragEnd', e);

    // dragState(false);
  }

  const rotateNum = 1;//Math.floor(Math.random()*1.5) + 0.5;
  const rand = Math.floor(Math.random()*100);
  const shakingMotion = { rotate: rand % 2 == 0 ? [0,rotateNum,0,-rotateNum,0] : [rotateNum,0,-rotateNum,0,rotateNum] }
  const stopMotion = { rotate: 0 }
 
  useEffect(() => {
    controls.start(shakingMotion);
  }, []);

  useEffect(() => {
    document.title = `${isSelected} : isSelected`;
    console.log('isSelected', isSelected)

    if(isSelected) {
      controls.start(stopMotion);
      setTimeout(()=> {
        controls.stop();
      }, 100);
      
    } else {
      controls.start(shakingMotion);
    }
  }, [isSelected]);

  return (
    <motion.div
      animate={controls}
      transition={{ delay: 0, repeat: Infinity, duration: 0.3, easeOut: [0.17, 0.67, 0.83, 0.67] }}
    >
      <Reorder.Item 
        value={item} 
        key={item.id} 
        whileTap={{ scale: 1.05 }} 
        onDrag={(e, { point }) => onDrag(e, point.y)}
        onDragEnd={e => onDragEnd(e)}
        dragElastic={1} 
        dragControls={dragControls}
        style={{ background: color, boxShadow, y }}>
          <span className="scoreLabel">{item.score}</span>
          <span>{item.quiz}</span>
      </Reorder.Item>
    </motion.div>
  );
};
