import * as React from "react";
import { useState, useEffect } from "react";
import { motion } from "framer-motion";
import QuizTitleBg from "../assets/images/qna.png";

interface IProps {
  id: number;
  step: number;
  curPage: string;
  next: Function;
  children: React.ReactNode;
  isDragging: boolean;
  selectHandling: Function;
}

export const Page: React.FC<IProps> = ({ id, curPage, step, next, children, isDragging, selectHandling }) => {
  const [disable, setDisable] = useState(id === 0 ? false:true);
  const [orderFixed, setOrderFixed] = useState(false);

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  useEffect(() => {
    setDisable(!isDragging);
    
    if(isDragging) {
      setOrderFixed(true);
    }
  }, [isDragging]);

  const nextPage = () => {
    next();

    console.log('Page nextPage :: step', step)
  }

  const fixOrder = () => {
    selectHandling(true);

    if(!orderFixed) {
      setOrderFixed(true);
    } else {
      setOrderFixed(false);
      next();
    }
  }

  return (
    <>
      <motion.div
        className="page"
        initial={{x: "100%"}}
        animate={{x: "0"}}
        exit={{x: "-100%"}}
      >
        
        {/*     */}
        { id >= 1 && id <= 9 &&
          <div className="quizTitleArea">
          <div className="numbering">
            <img src={QuizTitleBg} className="numImg" />
            <h1 className="quizNo">{id}<small>/9</small></h1>
          </div>
          
          <div className="bubble">
              <br/> <br/>-&gt;  <br/> ~
          </div>
        </div>}

        {children}

        <br/>
        <br/>
        {id == 0 && <button className="kolb-button mb10" onClick={nextPage}></button>}
        {id > 0 && id < 10 && 
          <button className={`mb10 kolb-button ${orderFixed || isDragging ? '':'outline'}`}
                  onClick={fixOrder}>{orderFixed || isDragging ? '':'  '}</button>}
      </motion.div>
    </>
  );
};
